












import {
	mapActions,
	mapState,
	mapGetters,
	mapMutations
} from 'vuex'
import CMore from '../../components/common/more'
import BlockBoxC from '../../components/bookblock/boxC'
export default {
	components: {
		CMore,
		BlockBoxC
	},
	computed: {
		...mapState('Base', ['Banner']),
	},
	methods: {
		...mapActions('Base', ['CommonFetch']),
	},
	async onLoad({
		id
	}) {
		await this.CommonFetch({
			url: `/store/v0/fiction/list/${id}`,
			name: 'Banner'
		})
	}
}
